<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process114"></a>

<p><br></p>
<p><span class="rvts17">传送装置（</span><span class="rvts50">The Teleporter</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:182,Y:168</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：盖温（</span><span class="rvts20">Gawin</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到传送手套</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1018.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">该任务在刚进入欢乐堡，遇到盖温后会自动触发，盖温一直在欢乐堡的集市场游荡。</span>
<br><span class="rvts21">当靠近他后，他会主动搭讪，并询问是否一个人。如果回答是一个人，那么他会告诉你鳄鱼偷走了传送头套。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1019.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">携带传送手套的鳄鱼位置在</span><span class="rvts20">X:128,Y:201</span><span class="rvts21">的位置，一共有三只鳄鱼。击杀鳄鱼后，可以获得传送手套。传送手套自带传送技能，因此是跨越地形的一大神器。传送手套还可以传送物品，但不可以传送施法者。</span>
<br><span class="rvts21">得到传送手套后，可以找盖温交任务，也可以不理会他，而一走了之。盖温是一个不折不扣的骗子，并且还十分的小心翼翼。如果在得到传送手套后再次找他，那么他会把带到一片悬崖旁。</span>
<br><span class="rvts21">这片悬崖就是我们在游戏初期遇到同伴费恩（</span><span class="rvts20">Fane</span><span class="rvts21">）的位置。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1020.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">盖温接着会让把他传送到悬崖下边，如果把他传送到最底层的沙地，那么他会狡诈的离开（进入监狱秘洞）。但事实证明，他的尸体会在折磨区找到。</span>
<br><span class="rvts21">如果在该任务之前已经清理了欢乐堡的船港，那么盖温的尸体会在下一章节的空虚沼泽中找到。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



